import React, { useState, useEffect } from 'react';
import { useParams, Link, useNavigate } from 'react-router-dom';
import { toast } from 'react-hot-toast';
import { Container, Row, Col, Card, Badge, Button, ListGroup, Form, Modal, Spinner } from 'react-bootstrap';
import api from '../services/api';
import CodeEditor from '../components/CodeEditor';

function TaskDetail({ api, teams, refreshTasks }) {
  const { taskId } = useParams();
  const navigate = useNavigate();
  
  const [task, setTask] = useState(null);
  const [loading, setLoading] = useState(true);
  const [executing, setExecuting] = useState(false);
  const [updating, setUpdating] = useState(false);
  const [error, setError] = useState(null);
  
  // Form için alt görev
  const [newSubtask, setNewSubtask] = useState('');
  const [subtaskTitle, setSubtaskTitle] = useState('');
  
  // Doküman yükleme için state
  const [showDocumentUpload, setShowDocumentUpload] = useState(false);
  const [newDocument, setNewDocument] = useState({
    title: '',
    content: '',
    type: 'text'
  });
  const [uploadingDocument, setUploadingDocument] = useState(false);
  const [evaluatingDocument, setEvaluatingDocument] = useState(false);
  
  // İterasyon modalı için state
  const [showIterationModal, setShowIterationModal] = useState(false);
  
  // Görev verilerini getir
  useEffect(() => {
    const fetchTaskDetails = async () => {
      setLoading(true);
      try {
        const response = await api.get(`/api/tasks/${taskId}`);
        setTask(response.data);
        setError(null);
      } catch (err) {
        console.error('Görev detayları alınırken hata:', err);
        setError('Görev bilgileri yüklenemedi. Lütfen daha sonra tekrar deneyin.');
      } finally {
        setLoading(false);
      }
    };
    
    fetchTaskDetails();
  }, [api, taskId]);
  
  // Alt görev ekleme
  const handleAddSubtask = async (e) => {
    e.preventDefault();
    
    if (!newSubtask.trim()) {
      toast.error('Alt görev açıklaması gereklidir!');
      return;
    }
    
    setUpdating(true);
    
    try {
      await api.post(`/api/tasks/${taskId}/subtasks/add`, { 
        description: newSubtask,
        title: subtaskTitle || undefined
      });
      
      // Görev bilgilerini güncellenmiş görevi kullan
      const taskResponse = await api.get(`/api/tasks/${taskId}`);
      setTask(taskResponse.data);
      await refreshTasks();
      
      toast.success('Alt görev başarıyla eklendi!');
      setNewSubtask('');
      setSubtaskTitle('');
    } catch (error) {
      console.error('Alt görev eklerken hata:', error);
      toast.error('Alt görev eklenemedi. Lütfen tekrar deneyin.');
    } finally {
      setUpdating(false);
    }
  };
  
  // Doküman formunun girdi değişiklikleri
  const handleDocumentInputChange = (e) => {
    const { name, value } = e.target;
    setNewDocument({ ...newDocument, [name]: value });
  };
  
  // Doküman yükleme
  const handleDocumentUpload = async (e) => {
    e.preventDefault();
    
    if (!newDocument.title.trim() || !newDocument.content.trim()) {
      toast.error('Doküman başlığı ve içeriği gereklidir!');
      return;
    }
    
    setUploadingDocument(true);
    
    try {
      await api.post(`/api/tasks/${taskId}/documents/upload`, newDocument);
      
      // Görev bilgilerini güncelle
      const taskResponse = await api.get(`/api/tasks/${taskId}`);
      setTask(taskResponse.data);
      await refreshTasks();
      
      toast.success('Doküman başarıyla yüklendi!');
      setNewDocument({ title: '', content: '', type: 'text' });
      setShowDocumentUpload(false);
    } catch (error) {
      console.error('Doküman yüklenirken hata:', error);
      toast.error('Doküman yüklenemedi. Lütfen tekrar deneyin.');
    } finally {
      setUploadingDocument(false);
    }
  };
  
  // Dokümanı değerlendir
  const handleEvaluateDocument = async (documentId) => {
    setEvaluatingDocument(true);
    
    try {
      await api.post(`/api/tasks/${taskId}/documents/${documentId}/evaluate`);
      
      // Görev bilgilerini güncelle
      const taskResponse = await api.get(`/api/tasks/${taskId}`);
      setTask(taskResponse.data);
      await refreshTasks();
      
      toast.success('Doküman başarıyla değerlendirildi!');
    } catch (error) {
      console.error('Doküman değerlendirilirken hata:', error);
      toast.error('Doküman değerlendirilemedi. Lütfen tekrar deneyin.');
    } finally {
      setEvaluatingDocument(false);
    }
  };
  
  // Görevi çalıştır
  const handleExecuteTask = async () => {
    try {
        setExecuting(true);
        await api.post(`/api/tasks/${taskId}/execute`);
        // response değişkenini kaldırıyoruz çünkü kullanılmıyor
        setTask(prev => ({ ...prev, status: 'in_progress' }));
        await refreshTasks();
        toast.success('Görev başarıyla çalıştırıldı!');
    } catch (error) {
        console.error('Görev çalıştırılırken hata:', error);
        toast.error('Görev çalıştırılamadı. Lütfen tekrar deneyin.');
    } finally {
        setExecuting(false);
    }
  };
  
  // Görev iterasyonu
  const handleIterateTask = async (feedback) => {
    try {
        setExecuting(true);
        await api.post(`/api/tasks/${taskId}/iterate`, { feedback });
        // response değişkenini kaldırıyoruz çünkü kullanılmıyor
        const taskResponse = await api.get(`/api/tasks/${taskId}`);
        setTask(taskResponse.data);
        await refreshTasks();
        toast.success('Görev iterasyonu başarıyla gerçekleştirildi!');
    } catch (error) {
        console.error('Görev iterasyonu sırasında hata:', error);
        toast.error('Görev iterasyonu yapılamadı. Lütfen tekrar deneyin.');
    } finally {
        setExecuting(false);
    }
  };
  
  // İterasyon modalını aç
  const handleOpenIterationModal = () => {
    setShowIterationModal(true);
  };
  
  // İterasyon modalını kapat
  const handleCloseIterationModal = () => {
    setShowIterationModal(false);
  };
  
  // Görevi sil
  const handleDeleteTask = async () => {
    if (window.confirm('Bu görevi silmek istediğinizden emin misiniz? Bu işlem geri alınamaz.')) {
      try {
        await api.delete(`/api/tasks/${taskId}`);
        await refreshTasks();
        toast.success('Görev başarıyla silindi!');
        navigate('/tasks');
      } catch (error) {
        console.error('Görev silinirken hata:', error);
        toast.error('Görev silinemedi. Lütfen tekrar deneyin.');
      }
    }
  };
  
  // Görev durumuna göre renk belirle
  const getStatusBadgeClass = (status) => {
    switch (status) {
      case 'completed':
        return 'bg-success';
      case 'in_progress':
        return 'bg-primary';
      case 'failed':
        return 'bg-danger';
      default:
        return 'bg-secondary';
    }
  };
  
  if (loading) {
    return (
      <div className="d-flex justify-content-center">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Yükleniyor...</span>
        </div>
      </div>
    );
  }
  
  if (error) {
    return (
      <div className="alert alert-danger" role="alert">
        {error}
      </div>
    );
  }
  
  if (!task) {
    return (
      <div className="alert alert-warning" role="alert">
        Görev bulunamadı. <Link to="/tasks">Görevlere dön</Link>
      </div>
    );
  }
  
  // İlgili takımı bul
  const team = teams?.find(t => t.id === task.team_id);
  
  return (
    <Container className="py-4">
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h1 className="h2">
          {task.name || task.description.substring(0, 30) + '...'}
          <span className={`ms-2 badge ${getStatusBadgeClass(task.status)}`}>
            {task.status || 'Yeni'}
          </span>
        </h1>
        <div>
          <button className="btn btn-danger me-2" onClick={handleDeleteTask}>Görevi Sil</button>
          <Link to="/tasks" className="btn btn-outline-secondary">Görevlere Dön</Link>
        </div>
      </div>
      
      <div className="row mb-4">
        <div className="col-md-8">
          <div className="card">
            <div className="card-header">
              Görev Detayları
            </div>
            <div className="card-body">
              <h5 className="card-title">Açıklama</h5>
              <p className="card-text">{task.description}</p>
              
              {task.created_at && (
                <p className="text-muted small">
                  Oluşturulma: {new Date(task.created_at).toLocaleString()}
                </p>
              )}
              
              {task.updated_at && (
                <p className="text-muted small">
                  Son Güncelleme: {new Date(task.updated_at).toLocaleString()}
                </p>
              )}
              
              {team && (
                <div className="mb-3">
                  <h5 className="card-title mt-4">Takım</h5>
                  <Link to={`/teams/${team.id}`} className="text-decoration-none">
                    <span className="badge bg-info me-2">{team.name}</span>
                  </Link>
                </div>
              )}
              
              <div className="mt-4">
                <h5 className="card-title">Görev İşlemleri</h5>
                <button 
                  className="btn btn-primary me-2" 
                  onClick={handleExecuteTask}
                  disabled={executing || task.status === 'in_progress'}
                >
                  {executing ? (
                    <>
                      <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                      Çalıştırılıyor...
                    </>
                  ) : (
                    'Görevi Çalıştır'
                  )}
                </button>
                
                <button 
                  className="btn btn-secondary" 
                  onClick={handleOpenIterationModal}
                  disabled={executing || task.status !== 'completed'}
                >
                  {executing ? (
                    <>
                      <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                      İşleniyor...
                    </>
                  ) : (
                    'İterasyon Başlat'
                  )}
                </button>
              </div>
              
              {/* Görev Sonucu */}
              {task.result && (
                <div className="mt-4">
                  <h5 className="card-title">Görev Sonucu</h5>
                  <div className="card">
                    <div className="card-body bg-light">
                      <pre className="mb-0" style={{ whiteSpace: 'pre-wrap', wordBreak: 'break-word' }}>
                        {task.result}
                      </pre>
                    </div>
                  </div>
                </div>
              )}
              
              {/* İterasyon Geçmişi */}
              {task.iterations && task.iterations.length > 0 && (
                <div className="mt-4">
                  <h5 className="card-title">İterasyon Geçmişi</h5>
                  <div className="accordion" id="iterationAccordion">
                    {task.iterations.map((iteration, index) => (
                      <div className="accordion-item" key={index}>
                        <h2 className="accordion-header">
                          <button 
                            className="accordion-button collapsed" 
                            type="button" 
                            data-bs-toggle="collapse" 
                            data-bs-target={`#iteration${index}`} 
                            aria-expanded="false" 
                            aria-controls={`iteration${index}`}
                          >
                            İterasyon {index + 1} - {new Date(iteration.timestamp).toLocaleString()}
                          </button>
                        </h2>
                        <div 
                          id={`iteration${index}`} 
                          className="accordion-collapse collapse" 
                          data-bs-parent="#iterationAccordion"
                        >
                          <div className="accordion-body">
                            {iteration.feedback && (
                              <div className="mb-3">
                                <strong>Geri Bildirim:</strong>
                                <p>{iteration.feedback}</p>
                              </div>
                            )}
                            
                            {iteration.previous_result && (
                              <div className="mb-3">
                                <strong>Önceki Sonuç:</strong>
                                <pre className="bg-light p-2" style={{ whiteSpace: 'pre-wrap', wordBreak: 'break-word' }}>
                                  {iteration.previous_result}
                                </pre>
                              </div>
                            )}
                            
                            {iteration.new_result && (
                              <div>
                                <strong>Yeni Sonuç:</strong>
                                <pre className="bg-light p-2" style={{ whiteSpace: 'pre-wrap', wordBreak: 'break-word' }}>
                                  {iteration.new_result}
                                </pre>
                              </div>
                            )}
                          </div>
                        </div>
                      </div>
                    ))}
                  </div>
                </div>
              )}
            </div>
          </div>
        </div>
        
        <div className="col-md-4">
          <div className="card mb-4">
            <div className="card-header d-flex justify-content-between align-items-center">
              <span>Alt Görevler</span>
              <button 
                className="btn btn-sm btn-outline-primary"
                onClick={() => setShowDocumentUpload(false)}
              >
                Alt Görev Ekle
              </button>
            </div>
            <div className="card-body">
              {!showDocumentUpload && (
                <form onSubmit={handleAddSubtask} className="mb-3">
                  <div className="mb-3">
                    <input
                      type="text"
                      className="form-control mb-2"
                      placeholder="Alt görev başlığı (opsiyonel)"
                      value={subtaskTitle}
                      onChange={(e) => setSubtaskTitle(e.target.value)}
                      disabled={updating}
                    />
                    <textarea
                      className="form-control"
                      placeholder="Alt görev açıklaması"
                      value={newSubtask}
                      onChange={(e) => setNewSubtask(e.target.value)}
                      disabled={updating}
                      rows="3"
                      required
                    ></textarea>
                  </div>
                  <button 
                    className="btn btn-primary w-100" 
                    type="submit"
                    disabled={updating}
                  >
                    {updating ? (
                      <>
                        <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                        Ekleniyor...
                      </>
                    ) : (
                      'Alt Görev Ekle'
                    )}
                  </button>
                </form>
              )}
              
              {task.subtasks && task.subtasks.length > 0 ? (
                <div className="list-group mt-3">
                  {task.subtasks.map((subtask, index) => (
                    <div className="list-group-item" key={index}>
                      <div className="d-flex justify-content-between align-items-center">
                        <h6 className="mb-1">{subtask.title || `Alt Görev #${index+1}`}</h6>
                        {subtask.completed && (
                          <span className="badge bg-success">Tamamlandı</span>
                        )}
                      </div>
                      <p className="mb-0">{subtask.description}</p>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-muted text-center mt-3">Henüz alt görev bulunmamaktadır.</p>
              )}
            </div>
          </div>
          
          <div className="card mb-4">
            <div className="card-header d-flex justify-content-between align-items-center">
              <span>Dokümanlar</span>
              <button 
                className="btn btn-sm btn-outline-success"
                onClick={() => setShowDocumentUpload(true)}
              >
                Doküman Yükle
              </button>
            </div>
            <div className="card-body">
              {showDocumentUpload && (
                <form onSubmit={handleDocumentUpload} className="mb-3">
                  <div className="mb-3">
                    <input
                      type="text"
                      className="form-control mb-2"
                      placeholder="Doküman başlığı"
                      name="title"
                      value={newDocument.title}
                      onChange={handleDocumentInputChange}
                      disabled={uploadingDocument}
                      required
                    />
                    <select
                      className="form-select mb-2"
                      name="type"
                      value={newDocument.type}
                      onChange={handleDocumentInputChange}
                      disabled={uploadingDocument}
                    >
                      <option value="text">Metin</option>
                      <option value="code">Kod</option>
                      <option value="specification">Spesifikasyon</option>
                      <option value="requirements">Gereksinimler</option>
                      <option value="design">Tasarım</option>
                    </select>
                    <textarea
                      className="form-control"
                      placeholder="Doküman içeriği"
                      name="content"
                      value={newDocument.content}
                      onChange={handleDocumentInputChange}
                      disabled={uploadingDocument}
                      rows="6"
                      required
                    ></textarea>
                  </div>
                  <div className="d-flex gap-2">
                    <button 
                      className="btn btn-success flex-grow-1" 
                      type="submit"
                      disabled={uploadingDocument}
                    >
                      {uploadingDocument ? (
                        <>
                          <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                          Yükleniyor...
                        </>
                      ) : (
                        'Doküman Yükle'
                      )}
                    </button>
                    <button 
                      className="btn btn-outline-secondary"
                      type="button" 
                      onClick={() => setShowDocumentUpload(false)}
                      disabled={uploadingDocument}
                    >
                      İptal
                    </button>
                  </div>
                </form>
              )}
              
              {task.documents && task.documents.length > 0 ? (
                <div className="list-group mt-3">
                  {task.documents.map((document, index) => (
                    <div className="list-group-item" key={index}>
                      <div className="d-flex justify-content-between align-items-center">
                        <h6 className="mb-1">{document.title}</h6>
                        <span className="badge bg-secondary">{document.type}</span>
                      </div>
                      <div className="mt-2 mb-2">
                        <div className="document-content" style={{ maxHeight: '100px', overflow: 'hidden' }}>
                          {document.content.substring(0, 150)}
                          {document.content.length > 150 && '...'}
                        </div>
                      </div>
                      <div className="d-flex gap-2">
                        <button
                          className="btn btn-sm btn-primary"
                          type="button"
                          data-bs-toggle="modal" 
                          data-bs-target={`#documentModal${index}`}
                        >
                          Görüntüle
                        </button>
                        <button
                          className="btn btn-sm btn-success"
                          type="button"
                          onClick={() => handleEvaluateDocument(document.id)}
                          disabled={evaluatingDocument}
                        >
                          {evaluatingDocument ? (
                            <span className="spinner-border spinner-border-sm" role="status" aria-hidden="true"></span>
                          ) : (
                            'Değerlendir'
                          )}
                        </button>
                      </div>
                      
                      {/* Doküman Modal */}
                      <div className="modal fade" id={`documentModal${index}`} tabIndex="-1" aria-hidden="true">
                        <div className="modal-dialog modal-lg modal-dialog-scrollable">
                          <div className="modal-content">
                            <div className="modal-header">
                              <h5 className="modal-title">{document.title}</h5>
                              <button type="button" className="btn-close" data-bs-dismiss="modal" aria-label="Kapat"></button>
                            </div>
                            <div className="modal-body">
                              <div className={document.type === 'code' ? 'code-block' : 'document-viewer'}>
                                <pre>{document.content}</pre>
                              </div>
                              
                              {/* Doküman Değerlendirmeleri */}
                              {task.document_evaluations && task.document_evaluations[document.id] && (
                                <div className="mt-4">
                                  <h5>Değerlendirmeler</h5>
                                  <div className="accordion" id={`evaluationAccordion${index}`}>
                                    {task.document_evaluations[document.id].map((evaluation, evalIndex) => (
                                      <div className="accordion-item" key={evalIndex}>
                                        <h2 className="accordion-header">
                                          <button 
                                            className="accordion-button collapsed" 
                                            type="button" 
                                            data-bs-toggle="collapse" 
                                            data-bs-target={`#evaluation${index}${evalIndex}`} 
                                            aria-expanded="false"
                                          >
                                            {evaluation.agent_name} ({evaluation.agent_role}) Değerlendirmesi
                                          </button>
                                        </h2>
                                        <div 
                                          id={`evaluation${index}${evalIndex}`} 
                                          className="accordion-collapse collapse" 
                                          data-bs-parent={`#evaluationAccordion${index}`}
                                        >
                                          <div className="accordion-body">
                                            <div className="team-evaluation">
                                              {evaluation.evaluation}
                                            </div>
                                            <div className="text-muted small text-end">
                                              {new Date(evaluation.timestamp).toLocaleString()}
                                            </div>
                                          </div>
                                        </div>
                                      </div>
                                    ))}
                                  </div>
                                </div>
                              )}
                            </div>
                            <div className="modal-footer">
                              <button type="button" className="btn btn-secondary" data-bs-dismiss="modal">Kapat</button>
                            </div>
                          </div>
                        </div>
                      </div>
                    </div>
                  ))}
                </div>
              ) : (
                <p className="text-muted text-center mt-3">Henüz doküman bulunmamaktadır.</p>
              )}
            </div>
          </div>

          {task.result && (
            <div className="card">
              <div className="card-header">
                Görev Sonuçları
              </div>
              <div className="card-body">
                <pre className="result-content p-3 bg-light rounded" style={{ whiteSpace: 'pre-wrap', fontSize: '0.85rem' }}>
                  {typeof task.result === 'string' ? task.result : JSON.stringify(task.result, null, 2)}
                </pre>
              </div>
            </div>
          )}
        </div>
      </div>
      
      {task.iterations && task.iterations.length > 0 && (
        <div className="card mt-4">
          <div className="card-header">
            İterasyonlar
          </div>
          <ul className="list-group list-group-flush">
            {task.iterations.map((iteration, index) => (
              <li className="list-group-item" key={index}>
                <div className="d-flex justify-content-between">
                  <h6>İterasyon #{iteration.number || index + 1}</h6>
                  <span className="text-muted small">
                    {iteration.timestamp ? new Date(iteration.timestamp).toLocaleString() : 'Tarih bilinmiyor'}
                  </span>
                </div>
                {iteration.feedback && (
                  <div className="mt-2">
                    <strong>Geri Bildirim:</strong>
                    <p>{iteration.feedback}</p>
                  </div>
                )}
                {iteration.response && (
                  <div className="mt-2">
                    <strong>Yanıt:</strong>
                    <pre className="p-2 bg-light rounded" style={{ whiteSpace: 'pre-wrap', fontSize: '0.85rem' }}>
                      {typeof iteration.response === 'string' ? iteration.response : JSON.stringify(iteration.response, null, 2)}
                    </pre>
                  </div>
                )}
              </li>
            ))}
          </ul>
        </div>
      )}
      
      {/* İterasyon Modalı */}
      {showIterationModal && (
        <div className="modal d-block" tabIndex="-1" role="dialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Görev İterasyonu</h5>
                <button type="button" className="btn-close" aria-label="Close" onClick={handleCloseIterationModal}></button>
              </div>
              <div className="modal-body">
                <form onSubmit={(e) => {
                  e.preventDefault();
                  const feedback = e.target.elements.feedback.value;
                  handleIterateTask(feedback);
                  handleCloseIterationModal();
                }}>
                  <div className="mb-3">
                    <label htmlFor="feedback" className="form-label">Geri Bildirim</label>
                    <textarea 
                      className="form-control" 
                      id="feedback" 
                      name="feedback" 
                      rows="5" 
                      placeholder="Görev sonucu hakkında geri bildiriminizi girin..."
                      required
                    ></textarea>
                  </div>
                  <div className="d-flex justify-content-end">
                    <button type="button" className="btn btn-secondary me-2" onClick={handleCloseIterationModal}>İptal</button>
                    <button type="submit" className="btn btn-primary">İterasyonu Başlat</button>
                  </div>
                </form>
              </div>
            </div>
          </div>
          <div className="modal-backdrop fade show"></div>
        </div>
      )}
      
      {/* Kod Editörü Bölümü */}
      {task && (
        <Row className="mt-4">
          <Col md={12}>
            <Card className="mb-4 shadow">
              <Card.Header className="d-flex justify-content-between align-items-center bg-primary text-white">
                <h5 className="mb-0">Kod Yönetimi</h5>
                <div>
                  <Link to={`/code-base/${taskId}`} className="btn btn-light btn-sm me-2">
                    <i className="bi bi-code-slash me-1"></i>
                    Kod Temeli
                  </Link>
                  <Link to={`/code-output/${taskId}`} className="btn btn-light btn-sm">
                    <i className="bi bi-terminal me-1"></i>
                    Çıktılar
                  </Link>
                </div>
              </Card.Header>
              <Card.Body>
                <CodeEditor taskId={taskId} onCodeUpdated={refreshTasks} />
              </Card.Body>
            </Card>
          </Col>
        </Row>
      )}
    </Container>
  );
}

export default TaskDetail; 