import React, { useState, useEffect } from 'react';
import { Card, Button, Form, Alert, Spinner, Tab, Tabs } from 'react-bootstrap';
import { Prism as SyntaxHighlighter } from 'react-syntax-highlighter';
import { dracula } from 'react-syntax-highlighter/dist/esm/styles/prism';
import api from '../services/api';
import { toast } from 'react-hot-toast';

const CodeEditor = ({ taskId, onCodeUpdated }) => {
  const [code, setCode] = useState('');
  const [language, setLanguage] = useState('python');
  const [fileName, setFileName] = useState('code.py');
  const [isRunning, setIsRunning] = useState(false);
  const [output, setOutput] = useState('');
  const [isSuccess, setIsSuccess] = useState(false);
  const [error, setError] = useState('');
  const [documents, setDocuments] = useState([]);
  const [activeDocument, setActiveDocument] = useState(null);
  const [isLoading, setIsLoading] = useState(false);

  useEffect(() => {
    // Görev dokümanlarını yükle
    if (taskId) {
      fetchDocuments();
    }
  }, [taskId]);

  useEffect(() => {
    const extensions = {
      python: 'py',
      javascript: 'js',
      typescript: 'ts',
      java: 'java',
      cpp: 'cpp',
      csharp: 'cs',
      php: 'php',
      ruby: 'rb',
      go: 'go',
      rust: 'rs'
    };
    
    const ext = extensions[language] || 'txt';
    const baseName = fileName.split('.')[0] || 'code';
    setFileName(`${baseName}.${ext}`);
  }, [language]);

  const fetchDocuments = async () => {
    try {
      setIsLoading(true);
      const response = await api.get(`/tasks/${taskId}/documents`);
      if (response.data && response.data.documents) {
        setDocuments(response.data.documents);
        
        // Eğer kodlar varsa ilk kodu seç
        const codeDocuments = response.data.documents.filter(doc => doc.type === 'code');
        if (codeDocuments.length > 0) {
          setActiveDocument(codeDocuments[0]);
          loadDocument(codeDocuments[0].id);
        }
      }
    } catch (error) {
      console.error('Dokümanlar yüklenirken hata:', error);
      setError('Dokümanlar yüklenemedi.');
    } finally {
      setIsLoading(false);
    }
  };

  const loadDocument = async (documentId) => {
    try {
      setIsLoading(true);
      const response = await api.get(`/tasks/${taskId}/documents/${documentId}`);
      if (response.data) {
        setCode(response.data.content);
        setFileName(response.data.title);
        
        // Dosya uzantısından dil tanıma
        const extension = response.data.title.split('.').pop().toLowerCase();
        const langMap = {
          'js': 'javascript',
          'py': 'python',
          'html': 'html',
          'css': 'css',
          'java': 'java',
          'php': 'php',
          'cs': 'csharp',
          'go': 'go',
          'rb': 'ruby',
          'rs': 'rust',
          'ts': 'typescript',
          'jsx': 'jsx',
          'tsx': 'tsx',
          'json': 'json',
          'xml': 'xml',
          'md': 'markdown',
          'sql': 'sql',
          'sh': 'bash',
          'yaml': 'yaml',
          'yml': 'yaml',
          'csv': 'csv'
        };
        
        setLanguage(langMap[extension] || 'text');
      }
    } catch (error) {
      console.error('Doküman yüklenirken hata:', error);
      setError('Doküman yüklenemedi.');
    } finally {
      setIsLoading(false);
    }
  };

  const handleCodeChange = (e) => {
    setCode(e.target.value);
    setOutput('');
    setError('');
    setIsSuccess(false);
  };

  const handleLanguageChange = (e) => {
    setLanguage(e.target.value);
  };

  const handleFileNameChange = (e) => {
    setFileName(e.target.value);
  };

  const handleRunCode = async () => {
    if (!code.trim()) {
      setError('Çalıştırmak için kod girmelisiniz');
      return;
    }
    
    setIsRunning(true);
    setOutput('');
    setError('');
    setIsSuccess(false);
    
    try {
      const response = await api.post(`/tasks/${taskId}/execute-code`, {
        code,
        language,
        fileName
      });
      
      if (response.data) {
        setOutput(response.data.output || 'Herhangi bir çıktı üretilmedi');
        setIsSuccess(true);
        toast.success('Kod başarıyla çalıştırıldı');
        
        // Eğer onCodeUpdated callback varsa çağır
        if (onCodeUpdated) {
          onCodeUpdated();
        }
      }
    } catch (error) {
      console.error('Kod çalıştırma hatası:', error);
      setError(error.response?.data?.detail || 'Kod çalıştırılırken bir hata oluştu');
      toast.error('Kod çalıştırılamadı');
    } finally {
      setIsRunning(false);
    }
  };

  const handleSaveCode = async () => {
    if (!code.trim()) {
      setError('Kaydetmek için kod girmelisiniz');
      return;
    }
    
    setIsLoading(true);
    setError('');
    
    try {
      const response = await api.post(`/tasks/${taskId}/documents/upload`, {
        title: fileName,
        content: code,
        type: 'code'
      });
      
      if (response.data) {
        setIsSuccess(true);
        toast.success('Kod başarıyla kaydedildi');
        
        // Eğer onCodeUpdated callback varsa çağır
        if (onCodeUpdated) {
          onCodeUpdated();
        }
      }
    } catch (error) {
      console.error('Kod kaydetme hatası:', error);
      setError(error.response?.data?.detail || 'Kod kaydedilirken bir hata oluştu');
      toast.error('Kod kaydedilemedi');
    } finally {
      setIsLoading(false);
    }
  };

  const getPlaceholderCode = () => {
    switch(language) {
      case 'python':
        return 'def main():\n    print("Merhaba, Dünya!")\n\nif __name__ == "__main__":\n    main()';
      case 'javascript':
        return 'function main() {\n    console.log("Merhaba, Dünya!");\n}\n\nmain();';
      case 'java':
        return 'public class Main {\n    public static void main(String[] args) {\n        System.out.println("Merhaba, Dünya!");\n    }\n}';
      default:
        return '// Kodunuzu buraya yazın';
    }
  };

  return (
    <Card className="mb-4 shadow">
      <Card.Header className="d-flex justify-content-between align-items-center bg-primary text-white">
        <div>
          <h5 className="mb-0">Kod Editörü</h5>
        </div>
        <div>
          <Button 
            variant="light" 
            size="sm" 
            className="me-2" 
            onClick={handleRunCode}
            disabled={isRunning || !code.trim()}
          >
            {isRunning ? (
              <>
                <Spinner animation="border" size="sm" className="me-1" />
                Çalıştırılıyor...
              </>
            ) : (
              <>
                <i className="bi bi-play-fill"></i> Çalıştır
              </>
            )}
          </Button>
          <Button 
            variant="success" 
            size="sm"
            onClick={handleSaveCode}
            disabled={isLoading || !code.trim()}
          >
            {isLoading ? (
              <>
                <Spinner animation="border" size="sm" className="me-1" />
                Kaydediliyor...
              </>
            ) : (
              <>
                <i className="bi bi-save"></i> Kaydet
              </>
            )}
          </Button>
        </div>
      </Card.Header>
      
      <Card.Body>
        {error && <Alert variant="danger">{error}</Alert>}
        {isSuccess && <Alert variant="success">İşlem başarıyla tamamlandı!</Alert>}
        
        <Tabs defaultActiveKey="editor" className="mb-3">
          <Tab eventKey="editor" title="Editör">
            <Form.Group className="mb-3 d-flex">
              <Form.Control
                className="me-2"
                type="text"
                placeholder="Dosya adı"
                value={fileName}
                onChange={handleFileNameChange}
              />
              <Form.Select 
                value={language}
                onChange={handleLanguageChange}
                style={{ width: '200px' }}
              >
                <option value="python">Python</option>
                <option value="javascript">JavaScript</option>
                <option value="java">Java</option>
                <option value="csharp">C#</option>
                <option value="cpp">C++</option>
                <option value="php">PHP</option>
                <option value="ruby">Ruby</option>
                <option value="go">Go</option>
                <option value="rust">Rust</option>
              </Form.Select>
            </Form.Group>
            
            <Form.Group className="mb-3">
              <Form.Control
                as="textarea"
                rows={15}
                value={code}
                onChange={handleCodeChange}
                placeholder={getPlaceholderCode()}
                className="font-monospace"
                style={{ 
                  backgroundColor: '#282a36', 
                  color: '#f8f8f2',
                  fontFamily: 'monospace'
                }}
              />
            </Form.Group>
          </Tab>
          
          <Tab eventKey="preview" title="Önizleme">
            <SyntaxHighlighter 
              language={language} 
              style={dracula}
              showLineNumbers={true}
            >
              {code || '// Henüz kod yok...'}
            </SyntaxHighlighter>
          </Tab>
          
          <Tab eventKey="output" title="Çıktı">
            <div 
              className="p-3 bg-dark text-light" 
              style={{ 
                height: '300px', 
                overflow: 'auto', 
                fontFamily: 'monospace' 
              }}
            >
              {output || 'Henüz çıktı yok...'}
            </div>
          </Tab>
          
          <Tab eventKey="files" title="Dosyalar">
            {isLoading ? (
              <div className="text-center p-4">
                <Spinner animation="border" />
                <p className="mt-2">Dosyalar yükleniyor...</p>
              </div>
            ) : documents.length > 0 ? (
              <div className="list-group">
                {documents.map(doc => (
                  <button
                    key={doc.id}
                    className={`list-group-item list-group-item-action d-flex justify-content-between align-items-center ${activeDocument?.id === doc.id ? 'active' : ''}`}
                    onClick={() => {
                      setActiveDocument(doc);
                      loadDocument(doc.id);
                    }}
                  >
                    <div>
                      <i className={`bi ${doc.type === 'code' ? 'bi-file-code' : 'bi-file-text'} me-2`}></i>
                      {doc.title}
                    </div>
                    <small>{new Date(doc.uploaded_at || Date.now()).toLocaleString()}</small>
                  </button>
                ))}
              </div>
            ) : (
              <p className="text-center text-muted">Henüz dosya yok.</p>
            )}
          </Tab>
        </Tabs>
      </Card.Body>
    </Card>
  );
};

export default CodeEditor; 