import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { toast } from 'react-hot-toast';

function Tasks({ api, tasks, teams, models, refreshTasks, isLoading }) {
  const [showCreateForm, setShowCreateForm] = useState(false);
  const [newTask, setNewTask] = useState({
    name: '',
    description: '',
    team_id: ''
  });
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setNewTask({ ...newTask, [name]: value });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    
    if (!newTask.description.trim() || !newTask.team_id) {
      toast.error('Açıklama ve takım gereklidir!');
      return;
    }
    
    setIsSubmitting(true);
    
    try {
      await api.post('/api/tasks/create', newTask);
      await refreshTasks();
      toast.success('Görev başarıyla oluşturuldu!');
      setNewTask({ name: '', description: '', team_id: '' });
      setShowCreateForm(false);
    } catch (error) {
      console.error('Görev oluştururken hata:', error);
      toast.error('Görev oluşturulamadı. Lütfen tekrar deneyin.');
    } finally {
      setIsSubmitting(false);
    }
  };

  // Görev durumuna göre filtreleme için durum renkleri
  const getStatusBadgeClass = (status) => {
    switch (status) {
      case 'completed':
        return 'bg-success';
      case 'in_progress':
        return 'bg-primary';
      case 'failed':
        return 'bg-danger';
      default:
        return 'bg-secondary';
    }
  };

  // Görevle ilişkili model ismini kontrol et
  const getModelNameForTask = (task) => {
    // İlgili takımı bul
    const team = teams?.find(t => t.id === task.team_id);
    if (!team || !team.agents || team.agents.length === 0) {
      return null;
    }
    
    // İlk ajanın modelini al
    return team.agents[0]?.model || null;
  };

  if (isLoading) {
    return (
      <div className="d-flex justify-content-center">
        <div className="spinner-border text-primary" role="status">
          <span className="visually-hidden">Yükleniyor...</span>
        </div>
      </div>
    );
  }

  return (
    <div>
      <div className="d-flex justify-content-between align-items-center mb-4">
        <h1 className="h2">Görevler</h1>
        <button 
          className="btn btn-primary"
          onClick={() => setShowCreateForm(!showCreateForm)}
          disabled={!teams || teams.length === 0}
        >
          {showCreateForm ? 'İptal' : 'Yeni Görev Oluştur'}
        </button>
      </div>

      {models && models.length === 0 && (
        <div className="alert alert-warning mb-4">
          Henüz kullanılabilir model bulunmamaktadır. Görevler için Ollama modelleri gereklidir.
        </div>
      )}

      {teams && teams.length === 0 && (
        <div className="alert alert-warning mb-4">
          Görev oluşturmak için önce bir takım oluşturmalısınız.
          <Link to="/teams" className="ms-2 btn btn-sm btn-outline-primary">Takımları Yönet</Link>
        </div>
      )}

      {showCreateForm && (
        <div className="card mb-4">
          <div className="card-header">
            Yeni Görev Oluştur
          </div>
          <div className="card-body">
            <form onSubmit={handleSubmit}>
              <div className="mb-3">
                <label htmlFor="name" className="form-label">Görev Adı</label>
                <input
                  type="text"
                  className="form-control"
                  id="name"
                  name="name"
                  value={newTask.name}
                  onChange={handleInputChange}
                  placeholder="Opsiyonel: Bu görev için bir isim verin"
                />
                <div className="form-text">İsterseniz boş bırakabilirsiniz, açıklama görev adı olarak kullanılacaktır.</div>
              </div>
              <div className="mb-3">
                <label htmlFor="description" className="form-label">Açıklama <span className="text-danger">*</span></label>
                <textarea
                  className="form-control"
                  id="description"
                  name="description"
                  value={newTask.description}
                  onChange={handleInputChange}
                  rows="3"
                  placeholder="Görevi detaylı bir şekilde tanımlayın"
                  required
                ></textarea>
              </div>
              <div className="mb-3">
                <label htmlFor="team_id" className="form-label">Takım <span className="text-danger">*</span></label>
                <select
                  className="form-select"
                  id="team_id"
                  name="team_id"
                  value={newTask.team_id}
                  onChange={handleInputChange}
                  required
                >
                  <option value="">Takım seçin</option>
                  {teams && teams.map((team, index) => (
                    <option key={index} value={team.id}>
                      {team.name} ({team.agents?.length || 0} ajan)
                    </option>
                  ))}
                </select>
              </div>
              <button
                type="submit"
                className="btn btn-success"
                disabled={isSubmitting}
              >
                {isSubmitting ? (
                  <>
                    <span className="spinner-border spinner-border-sm me-2" role="status" aria-hidden="true"></span>
                    Oluşturuluyor...
                  </>
                ) : (
                  'Görev Oluştur'
                )}
              </button>
            </form>
          </div>
        </div>
      )}

      {tasks && tasks.length > 0 ? (
        <div className="table-responsive">
          <table className="table table-striped table-hover">
            <thead className="table-light">
              <tr>
                <th scope="col">ID</th>
                <th scope="col" style={{ width: '30%' }}>Görev</th>
                <th scope="col">Takım</th>
                <th scope="col">Model</th>
                <th scope="col">Durum</th>
                <th scope="col">Son Güncelleme</th>
                <th scope="col" style={{ width: '120px' }}>İşlemler</th>
              </tr>
            </thead>
            <tbody>
              {tasks.map((task, index) => {
                // İlgili takımı bul
                const team = teams?.find(t => t.id === task.team_id);
                // Kullanılan modeli bul
                const modelName = getModelNameForTask(task);
                
                return (
                  <tr key={index}>
                    <td>
                      <small className="text-muted">{task.id.substring(0, 8)}...</small>
                    </td>
                    <td>
                      <Link to={`/tasks/${task.id}`} className="text-decoration-none fw-semibold">
                        {task.name || task.description.substring(0, 60) + '...'}
                      </Link>
                    </td>
                    <td>
                      {team ? (
                        <Link to={`/teams/${team.id}`} className="text-decoration-none">
                          {team.name}
                        </Link>
                      ) : (
                        <span className="text-muted">Bilinmiyor</span>
                      )}
                    </td>
                    <td>
                      {modelName ? (
                        <span className="badge bg-info">{modelName}</span>
                      ) : (
                        <span className="text-muted">-</span>
                      )}
                    </td>
                    <td>
                      <span className={`badge ${getStatusBadgeClass(task.status)}`}>
                        {task.status || 'Yeni'}
                      </span>
                    </td>
                    <td>
                      <small className="text-muted">
                        {task.updated_at ? new Date(task.updated_at).toLocaleString() : 'Bilinmiyor'}
                      </small>
                    </td>
                    <td>
                      <Link to={`/tasks/${task.id}`} className="btn btn-sm btn-outline-primary">
                        Görüntüle
                      </Link>
                    </td>
                  </tr>
                );
              })}
            </tbody>
          </table>
        </div>
      ) : (
        <div className="alert alert-info">
          Henüz görev bulunmamaktadır. Yeni bir görev oluşturmak için yukarıdaki butonu kullanabilirsiniz.
        </div>
      )}
    </div>
  );
}

export default Tasks; 